.search-results__item {
  @include null-margins;

  + .search-results__item {
    border-top: 1px solid $border--light;
    margin-top: unit(1.5);
    padding-top: unit(1.5);
  }
}

.search-results__date,
.search-results__author,
.search-results__meta {
  display: block;
  font-size: 1.2rem;
  line-height: 1.2;
}

.search-results__meta {
  flex-shrink: 0;
  font-weight: $font-weight--bold;
  line-height: unit(1);
  text-align: right;

  &.-download a {
    background-image: url(#{$path}/images/icon-download.svg);
    padding-left: 21px;
  }

  &.-link a {
    background-image: url(#{$path}/images/icon-link.svg);
    padding-left: 20px;
  }

  &.-pdf a {
    background-image: url(#{$path}/images/icon-pdf.svg);
    padding-left: 22px;
  }

  &.-video a {
    background-image: url(#{$path}/images/icon-video.svg);
    padding-left: 25px;
  }

  a {
    @include hover-underline;
    background-position: left 3px;
    background-repeat: no-repeat;
    color: $color--tuatara;
    display: inline-block;
  }
}

.search-results__details {
  display: flex;
  margin: 0 0 unit(0.3);
  width: 100%;
}

.search-results__date {
  flex-grow: 1;
  line-height: unit(1);
}

.search-results__datetime {
  display: inline-block;
  margin-right: unit(0.2);
}

.search-results__archived {
  @include smooth-text;
  background-color: rgba($color--jelly-bean, 0.2);
  border-radius: 2px;
  color: $color--sherpa-blue;
  display: inline-block;
  font: 1.3rem/20px $font-family--kapra-expanded;
  margin-right: unit(0.5);
  padding: 2px unit(0.4) 0;
  text-transform: uppercase;
}

.search-results__title {
  font: 400 2.4rem/1.4 $font-family--serif;
  margin: 0 0 unit(0.5);

  a {
    @include hover-underline;
    color: $color--jelly-bean;
  }
}

.search-results__author {
  font-weight: $font-weight--bold;
  margin: unit(0.5) 0;
}

.search-results__blurb {
  margin: unit(1) 0 unit(0.5);
}

.search-results__taxonomy {
  margin-left: unit(-1);
  margin-top: unit(0.5);

  span {
    display: inline-block;
    font-size: 1.2rem;
    font-weight: $font-weight--bold;
    margin-left: unit(1);
    text-transform: uppercase;
    white-space: nowrap;
  }
}
